import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Button, Form } from 'react-bootstrap';
import Slider from 'react-input-slider';

import { Card, Stack } from 'react-bootstrap';
import { Alert } from 'react-bootstrap';
import { Navbar, Nav } from 'react-bootstrap';
import { BsFillTrash3Fill, BsSave } from "react-icons/bs";

import badCombinationMeat from './badCombinationMeat.json';
import allergenVeggies from './allergenVeggies.json';
import ingredients from './ingredients.json';
import babyTips from './babyTips.json';

const Ranking = ({ rankingData }) => {
  const [expanded, setExpanded] = useState(Array(rankingData.length).fill(false));

  const toggleExpand = (index) => {
    const newExpanded = [...expanded];
    newExpanded[index] = !newExpanded[index];
    setExpanded(newExpanded);
  };

  return (
    <div className="ranking-container">
      <h2 className="text-center">이유식 인기순위</h2>
      <ol style={{ paddingInlineStart: '0px', listStyleType: 'none' }}>
        {rankingData.map((item, index) => (
          <li key={item.rank} style={{
            display: 'flex',
            alignItems: 'center',
            padding: '10px 20px',
            margin: '10px',
            borderRadius: '10px',
            backgroundColor: index % 2 === 0 ? '#f3f3f3' : '#ffffff',
            border: '1px solid #e0e0e0'
          }}>
            <span style={{
              fontWeight: 'bold',
              fontSize: '18px',
              width: '60px',
              textAlign: 'center'
            }}>{item.rank}위</span>
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              justifyContent: 'space-between',
              width: 'calc(100% - 70px)',
              overflow: 'hidden'
            }}>
              <span style={{
                fontSize: '16px',
                whiteSpace: expanded[index] ? 'normal' : 'nowrap',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                maxWidth: '80%'
              }}>{item.menu}</span>
              {item.menu.length > 20 &&
                <span
                  onClick={() => toggleExpand(index)}
                  style={{
                    cursor: 'pointer',
                    textDecoration: 'underline',
                    color: '#007bff',
                    fontSize: '14px',
                    marginLeft: '10px'
                  }}
                >
                  {expanded[index] ? '닫기' : '더 보기'}
                </span>}
            </div>
          </li>
        ))}
      </ol>
    </div>
  );
};


const BottomNavbar = ({ onRecipeClick, onRankingClick }) => {
  return (
    <Navbar fixed="bottom" bg="primary" className="justify-content-around" style={{ paddingTop: '20px', paddingBottom: '50px', borderTopLeftRadius: '20px', borderTopRightRadius: '20px' }}>
      <Nav.Link style={{color:'white'}} href="#recipe" onClick={(event) => {event.preventDefault(); onRecipeClick();}}>저장된 레시피</Nav.Link>
      <Nav.Link style={{color:'white'}} href="#ranking" onClick={(event) => {event.preventDefault(); onRankingClick();}}>이유식 인기순위</Nav.Link>
    </Navbar>
  );
};

const MealPlan = ({ mealPlan, babyMonth, stage, onRegeneratePlan, onMealSave, onMealDelete, isRecipeTab }) => {
  const getSubjectParticle = (word) => {
    const lastChar = word.charAt(word.length - 1);
    // If the last character is a vowel, return '은'; otherwise, return '는'
    return ['가', '나', '다', '라', '대', '린','림', '마', '망', '바', '사', '차', '콩', '타', '파', '하'].includes(lastChar) ? '은' : '는';
  };
    
  const checkBadCombination = (meal) => {
    const ingredients = meal.map(item => item.ingredient);
    for (let i = 0; i < ingredients.length; i++) {
      if (badCombinationMeat[ingredients[i]]) {
        const badCombination = badCombinationMeat[ingredients[i]].find(ingredient => ingredients.includes(ingredient));
        if (badCombination) {
          return `${ingredients[i]}와 ${badCombination}은 좋지 않은 궁합`;
        }
      }
    }
    
    return null;
  };

  const checkAllergen = (meal) => {
    const ingredients = meal.map(item => item.ingredient);
    const allergens = allergenVeggies.filter(ingredient => ingredients.includes(ingredient));
    if (allergens.length > 0) {
      const allergenList = allergens.join(', ');
      const particle = getSubjectParticle(allergenList);
      return `${allergenList}${particle} 알레르기 유발할 수 있음`;
    }
    return null;
  };
  
    // mealPlan이 배열인지 확인
    if (!Array.isArray(mealPlan)) {
      return <div>Error: mealPlan must be an array!</div>;
    }
    
  return (
    <div>
      <h2 className="text-center" style={{ marginTop: '50px' }}>
        {babyMonth ? `${babyMonth}개월 아기, ` : ''}{stage} 식단표
        </h2>
      <Row xs={1} md={2} className="g-4" style={{ marginTop: '10px' }}>
        {mealPlan.map((meal, index) => {
          const badCombinationWarning = checkBadCombination(meal);
          const allergenWarning = checkAllergen(meal);
          return (
            <Col>
              <Card>
                <Card.Body>
                  <Card.Title>메뉴 {index + 1} 
                  {!isRecipeTab && <BsSave style={{ marginLeft: '10px'}} onClick={() => onMealSave(meal)} />}
                  {!isRecipeTab && <BsFillTrash3Fill onClick={() => onMealDelete(meal)} style={{ marginLeft: '30px', color: 'red'}}/>}
                  {isRecipeTab  && <BsFillTrash3Fill onClick={() => onMealDelete(meal)} style={{ marginLeft: '30px', color: 'red'}}/>}                  
                  </Card.Title>
                  <Card.Text>
                  식재료: {meal.filter(item => item.ingredient !== undefined).map(item => `${item.ingredient} (${item.category})`).join(', ')}
                  </Card.Text>
                  {badCombinationWarning && <Alert variant="warning">{badCombinationWarning}</Alert>}
                  {allergenWarning && <Alert variant="danger">{allergenWarning}</Alert>}
                </Card.Body>
              </Card>
            </Col>
          );
        })}
      </Row>
      <Row className="text-center">
      {!isRecipeTab ? (
        <div className="d-flex justify-content-center mt-3" style={{ marginBottom: '70px' }}>
          <Col>
            <Button variant="success" onClick={onRegeneratePlan}>
              다시 생성
            </Button>
          </Col>
          <Col>
            <Button variant="primary" style={{ marginBottom: '70px' }} onClick={() => window.location.reload()}>
              처음으로
            </Button>
          </Col>
        </div>
      ) : (
        <div style={{ marginBottom: '120px' }}>
          <Col>
            <Button variant="primary" style={{ marginBottom: '120px' }} onClick={() => window.location.reload()}>
              메뉴 생성하기
            </Button>
          </Col>
        </div>
      )}
      </Row>
    </div>
  );
};
const generateMeal = (selectedCategories, babyMonth, selectedIngredients, selectedMenuCount) => {
  const meal = [];

  // 사용자가 선택한 식재료 중에서 무작위로 선택
  while (meal.length < selectedMenuCount) {
    const categoryIndex = Math.floor(Math.random() * selectedCategories.length);
    const category = selectedCategories[categoryIndex];

    if (selectedIngredients[category] && selectedIngredients[category].length > 0) {
      const ingredientIndex = Math.floor(Math.random() * selectedIngredients[category].length);
      const ingredient = selectedIngredients[category][ingredientIndex];

      // 중복 식재료를 피하기 위해 선택한 식재료 제거
      selectedIngredients[category].splice(ingredientIndex, 1);

      meal.push({ ingredient, category });
    } else {
      // 사용 가능한 식재료가 없는 경우
      return null; // 중요: null 반환
    }
  }

  return meal;
};

const generateMealPlan = (menuCount, selectedMenuCount, selectedCategories, babyMonth, selectedIngredients, ingredients) => {
  const mealPlan = [];

  // 메뉴 수만큼 반복하여 식단 생성
  for (let i = 0; i < menuCount; i++) {
    const meal = generateMeal(selectedCategories, babyMonth, JSON.parse(JSON.stringify(selectedIngredients)), selectedMenuCount); // 선택된 식재료의 복사본을 전달
    if (!meal) {
      return { error: `더 많은 식재료를 선택해주세요. ${selectedMenuCount}개의 추가 식재료가 필요합니다.` };
    }
    mealPlan.push(meal);
  }

  return mealPlan;
};




const FoodSelectionForm = ({ onFormSubmit }) => {
  const [step, setStep] = useState(1);
  const [babyMonth, setBabyMonth] = useState(8);
  const [menuCount, setMenuCount] = useState(4);
  const [selectedMenuCount, setSelectedMenuCount] = useState(3);
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedIngredients, setSelectedIngredients] = useState({});
  
  // Add a function to determine the stage based on the baby's month
  const determineStage = (babyMonth) => {
    if (babyMonth <= 6) {
      return "초기";
    } else if (babyMonth <= 9) {
      return "중기";
    } else if (babyMonth <= 12) {
      return "후기";
    } else {
      return "완료기";
    }
  };
   
  const handleFormSubmit = (event) => {
    event.preventDefault();
  
    // 식재료 종류 선택 단계에서 검사합니다.
    if (step === 2 && selectedCategories.length === 0) {
      alert('최소 한 가지 이상의 식재료 종류를 선택해주세요.');
      return;
    }
    // 식재료 선택 단계에서 검사합니다.
    if (step >= 3 && step < selectedCategories.length + 3) {
      const currentCategory = selectedCategories[step - 3];
      if (!selectedIngredients[currentCategory] || selectedIngredients[currentCategory].length === 0) {
        alert(`'${currentCategory}' 범주에서 최소 한 가지 이상의 식재료를 선택해주세요.`);
        return;
      }
    }
    if (step === selectedCategories.length + 3) {
      // 선택된 식재료의 총 개수 확인
      const totalSelectedIngredients = Object.values(selectedIngredients).reduce((total, arr) => total + arr.length, 0);
    
      // 선택된 식재료의 총 개수가 최소 개수보다 적은 경우
      if (totalSelectedIngredients < 1) {
        alert('최소 2개의 식재료를 선택해주세요.');
        return;
      }
      setStep(step + 1);
    } else if (step === selectedCategories.length + 5) {
      // 식재료의 총 개수 확인
      const totalSelectedIngredients = Object.values(selectedIngredients).reduce((total, arr) => total + arr.length, 0);
      const requiredIngredients = selectedMenuCount * menuCount;
      
      if (totalSelectedIngredients < requiredIngredients) {
        const lackingIngredients = requiredIngredients - totalSelectedIngredients; // 부족한 식재료의 개수 계산
        alert(`총 식재료 개수가 부족합니다. ${lackingIngredients+1}개의 식재료를 더 선택해주세요.`);
        setStep(2); // "식재료 범주 선택" 단계로 돌아갑니다.
        return;
      }
      
      // 식단표 생성
      const result = generateMealPlan(menuCount, selectedMenuCount, selectedCategories, babyMonth, selectedIngredients, ingredients);
      if (result) {
        const stage = determineStage(babyMonth);
        onFormSubmit(menuCount, selectedMenuCount, selectedCategories, selectedIngredients, babyMonth, stage, result);
      }
    } else {
      // 그 외의 경우
      setStep(step + 1);
    }
  };
  
  
  const handleIngredientSelection = (category, value) => {
    setSelectedIngredients((prevIngredients) => {
      // Check if the category already has selected ingredients
      if (prevIngredients[category]) {
        // If the ingredient is already selected, remove it
        if (prevIngredients[category].includes(value)) {
          return {
            ...prevIngredients,
            [category]: prevIngredients[category].filter((item) => item !== value),
          };
        }
        // If the ingredient is not selected, add it
        else {
          return {
            ...prevIngredients,
            [category]: [...prevIngredients[category], value],
          };
        }
      }
      // If the category does not have any selected ingredients, add the new ingredient
      else {
        return {
          ...prevIngredients,
          [category]: [value],
        };
      }
    });
  };
  
  // 이전 버튼을 처리하기 위한 함수를 추가합니다.
  const handlePreviousStep = () => {
    setStep(step - 1);
  };

  const [tip, setTip] = useState('');

  useEffect(() => {
    const randomIndex = Math.floor(Math.random() * babyTips.length);
    setTip(babyTips[randomIndex]);
  }, [step]); // step이 변경될 때마다 새 팁을 선택합니다.
 
    
  return (
    <Form className="container-sm" onSubmit={handleFormSubmit} style={{ marginTop: '50px' }}>
      {step === 1 && (
        <Form.Group className="mb-3" controlId="formBabyMonth">
          <h2><Form.Label>아기의 나이를 선택하세요</Form.Label></h2>
          <Slider
            axis="x"
            x={babyMonth}
            xmin={4}
            xmax={12}
            onChange={({ x }) => setBabyMonth(x)}
            styles={{
              track: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              },
              active: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              }
            }}
          />
          <div>선택된 개월 수: {babyMonth}</div>
        </Form.Group>
      )}
      {step === 2 && (
        <Form.Group className="mb-3" controlId="formSelectedCategories">
          <h2><Form.Label>식재료 종류를 선택하세요</Form.Label></h2>
          <div style={{ display: 'flex', flexDirection: 'column'}}>

              {Object.keys(ingredients).map((category) => {
                let categoryItems = [];
                for (let month = 4; month <= babyMonth; month++) {
                  if (ingredients[category][month]) {
                    categoryItems = categoryItems.concat(ingredients[category][month]);
                  }
                }
                if (categoryItems.length > 0) {
                  return (
                    <div style={{ width: '40%', margin: '4px 0' }}>
                      <Form.Check className="fs-5"
                        type="checkbox"
                        id={category} // 추가한 id 속성
                        label={category}
                        value={category}
                        checked={selectedCategories.includes(category)} // 이 부분을 수정
                        onChange={(e) => {
                          const value = e.target.value;
                          setSelectedCategories((prevCategories) =>
                            prevCategories.includes(value)
                              ? prevCategories.filter((category) => category !== value)
                              : [...prevCategories, value]
                            );
                          }}
                      />
                    </div>
                  );
                }
                return null; // categoryItems.length가 0인 경우 null을 반환
              })}
          </div>
        </Form.Group>
      )}
      {step === selectedCategories.length + 3 && (
        <div>
          <h2>Tip</h2>
          <div style={{ marginBottom: '10px' }}>
            {tip}
          </div>
          {/* 이곳에 리뷰를 남길 수 있는 폼이나 다른 요소들을 추가하면 됩니다. */}
        </div>
      )}
      {step >= 3 && step < selectedCategories.length + 3 && (
        (() => {
          const category = selectedCategories[step - 3];
          let categoryItems = [];
          for (let month = 4; month <= babyMonth; month++) {
            if (ingredients[category][month]) {
              categoryItems = categoryItems.concat(ingredients[category][month]);
            }
          }

          const handleSelectAll = (e) => {
            const isChecked = e.target.checked;
            const allItems = categoryItems;
            setSelectedIngredients((prevIngredients) => ({
              ...prevIngredients,
              [category]: isChecked ? allItems : [],
            }));
          };

          // Check if all items are selected
          const allSelected = selectedIngredients[category] && selectedIngredients[category].length === categoryItems.length;

          return (
            <Form.Group className="mb-3" controlId={`formSelectedIngredients-${category}`}>
              <h2>
                <Form.Label>{category}의 식재료를 선택하세요</Form.Label>
              </h2>
              <div style={{ display: 'flex', flexDirection: 'column' }}>
                <Form.Check
                  type="checkbox"
                  id={`selectAll-${category}`}
                  label={<span style={{ fontWeight: 'bold' }}>전체 선택</span>}
                  checked={allSelected}
                  onChange={handleSelectAll}
                />
                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                  {categoryItems.map((item) => (
                    <div style={{ width: '50%', margin: '4px 0' }}>
                      <Form.Check
                        className="fs-6" // 라벨의 크기를 조정하는 클래스를 추가합니다.
                        type="checkbox"
                        id={item}
                        label={item}
                        value={item}
                        checked={selectedIngredients[category] ? selectedIngredients[category].includes(item) : false}
                        onChange={(e) => handleIngredientSelection(category, e.target.value)}
                      />
                    </div>
                  ))}
                </div>
              </div>
            </Form.Group>
          );
        })()
      )}
      {step === Math.max(4, selectedCategories.length + 4) && (
        <Form.Group className="mb-3" controlId="formSelectedMenuCount">
          <h2><Form.Label>식재료의 개수를 선택하세요</Form.Label></h2>
          <Slider
            axis="x"
            x={selectedMenuCount}
            xmin={1}
            xmax={5}
            onChange={({ x }) => setSelectedMenuCount(x)}
            styles={{
              track: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              },
              active: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              }
            }}
          />
          <div>선택된 조합 식재료 종류: {selectedMenuCount}</div>
        </Form.Group>
      )}
      {step === selectedCategories.length + 5 && (   
        <Form.Group className="mb-3" controlId="formMenuCount">
          <h2><Form.Label>생성할 메뉴 개수를 선택하세요</Form.Label></h2>
          <Slider
            axis="x"
            x={menuCount}
            xmin={1}
            xmax={7}
            onChange={({ x }) => setMenuCount(x)}
            styles={{
              track: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              },
              active: {
                height: '14px', // 높이를 원하는 크기로 설정
                width: '100%'
              }
            }}
          />
          <div>선택된 메뉴 개수: {menuCount}</div>
        </Form.Group>
      )}
      <Stack gap={5}>
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        {/* 이전 버튼을 추가합니다. 첫 단계에서는 보이지 않게 합니다. */}
        {step > 1 && (
          <Button variant="secondary" onClick={handlePreviousStep} style={{ width: '48%' }}>
            이전
          </Button>
        )}
        <Button
          variant="primary"
          type="submit"
          style={{
            width: step === 1 ? '100%' : '48%',
            marginLeft: step > 1 ? '10px' : '0px',
          }}
        >
          {step === selectedCategories.length + 5 ? '식단표 생성하기' : '다음'}
        </Button>
      </div>
      <h3 className="text-center" style={{ marginBottom: '90px' }}>{step} / {selectedCategories.length + 5}</h3>
    </Stack>
  </Form>
  );
};

const App = () => {
  const [isRecipeTab, setIsRecipeTab] = useState(false);
  const [dietPlan, setDietPlan] = useState(null);
  const rankingData = [
    { menu: "닭고기와 쌀, 당근 볶음 (육류, 곡류, 채소류)", rank: 1 },
    { menu: "연어와 감자, 시금치 스팀 (수산물, 채소류)", rank: 2 },
    { menu: "소고기와 브로콜리 스튜 (육류, 채소류)", rank: 3 },
    { menu: "배와 바나나 믹스 (과일류)", rank: 4 },
    { menu: "토마토와 스위트콘 샐러드 (채소류)", rank: 5 },
    { menu: "호박죽 (곡류)", rank: 6 },
    { menu: "달걀과 치즈 오믈렛 (유제품)", rank: 7 },
    { menu: "두부와 시금치 볶음 (콩류, 채소류)", rank: 8 },
    { menu: "아몬드와 견과류 믹스 (견과류)", rank: 9 },
    { menu: "새우와 미역국 (수산물)", rank: 10 }
  ];
  
  const [showRanking, setShowRanking] = useState(false); // 랭킹 탭 활성화 상태 관리

    // 랭킹 탭 클릭 이벤트 핸들러
    const handleRankingClick = () => {
      setShowRanking(true);
      setIsRecipeTab(false); // 레시피 탭 비활성화
    };

    
  const handleMealSave = (meal) => {
    // Save the meal to localStorage
    let savedMeals = localStorage.getItem('savedMeals');
    savedMeals = savedMeals ? JSON.parse(savedMeals) : [];
    savedMeals.push(meal);
    localStorage.setItem('savedMeals', JSON.stringify(savedMeals));
  
    // Remove the saved meal from the diet plan
    const newDietPlan = dietPlan.meals.filter(savedMeal => JSON.stringify(savedMeal) !== JSON.stringify(meal));
    setDietPlan({ meals: newDietPlan, babyMonth: dietPlan.babyMonth, stage: dietPlan.stage });
  };
  const handleMealDelete = (meal) => {
    // Load the saved meals from localStorage
    let savedMeals = localStorage.getItem('savedMeals');
    savedMeals = savedMeals ? JSON.parse(savedMeals) : [];
    
    // Filter out the selected meal
    savedMeals = savedMeals.filter(savedMeal => JSON.stringify(savedMeal) !== JSON.stringify(meal));
    
    // Save the updated meal list back to localStorage
    localStorage.setItem('savedMeals', JSON.stringify(savedMeals));
    
    // Update the diet plan in the state
    const newDietPlan = dietPlan.meals.filter(savedMeal => JSON.stringify(savedMeal) !== JSON.stringify(meal));
    setDietPlan({ meals: newDietPlan, babyMonth: dietPlan.babyMonth, stage: dietPlan.stage });
  };
  
  
  useEffect(() => {
    if (isRecipeTab) {
      handleLoadSavedMeals();
    }
  }, [isRecipeTab]);

  const handleLoadSavedMeals = () => {
    // Load the saved meals from localStorage
    let savedMeals = localStorage.getItem('savedMeals');
    savedMeals = savedMeals ? JSON.parse(savedMeals) : [];
    setDietPlan({ meals: savedMeals, babyMonth: null, stage: '저장된 ' });
    setIsRecipeTab(true); // 수정: isRecipeTab 상태를 true로 변경
    setShowRanking(false); // 랭킹 화면 숨기기
  };
  
  const handleFormSubmit = (menuCount, selectedMenuCount, selectedCategories, selectedIngredients, babyMonth, stage) => {
    const result = generateMealPlan(menuCount, selectedMenuCount, selectedCategories, babyMonth, selectedIngredients, ingredients);
  
    if (result.error) {
      alert(result.error);
      setDietPlan({ meals: [], babyMonth, stage });
    } else {
      setDietPlan({ meals: result, babyMonth, stage });
    }
  
    // 마지막으로 선택한 입력 값을 저장합니다.
    setLastInputValues({ menuCount, selectedMenuCount, selectedCategories, selectedIngredients, babyMonth, stage });
  };
  const [lastInputValues, setLastInputValues] = useState(null);
  const handleRegeneratePlan = () => {
    if (isRecipeTab) {
      // If the user is in the recipe tab, reset to the initial form
      setDietPlan(null);
      setIsRecipeTab(false);
      setLastInputValues(null);
    } else if (lastInputValues) {
      const { menuCount, selectedMenuCount, selectedCategories, babyMonth, stage } = lastInputValues;

      // 선택된 식재료의 복사본을 생성합니다.
      const selectedIngredientsCopy = JSON.parse(JSON.stringify(lastInputValues.selectedIngredients));
  
      const plan = generateMealPlan(menuCount, selectedMenuCount, selectedCategories, babyMonth, selectedIngredientsCopy, ingredients);
  
      // Check if the plan generation was successful
      if (plan.error) {
        alert(plan.error);
      } else {
        setDietPlan({ meals: plan, babyMonth, stage });
      }
    } else {
      alert('이전에 선택된 값이 없어 식단표를 다시 생성할 수 없습니다.');
    }
  };
  
  return (
    <Container className="App">
      {showRanking ? (
        <Ranking rankingData={rankingData}/>
      ) : !dietPlan ? (
        <Row xxl className="container-fluid">
          <Col xxl>
            <FoodSelectionForm onFormSubmit={handleFormSubmit} />
          </Col>
        </Row>
      ) : (
        <div className="flex-grow-1 padded-top">
          <Row className="justify-content-md-center">
            <Col xxl={1}>
              <MealPlan
                mealPlan={dietPlan.meals}
                babyMonth={dietPlan.babyMonth}
                stage={dietPlan.stage}
                onRegeneratePlan={handleRegeneratePlan}
                onMealSave={handleMealSave}
                onMealDelete={handleMealDelete}
                isRecipeTab={isRecipeTab}
              />
            </Col>
          </Row>
        </div>
      )}
      <BottomNavbar className="fixed-bottom-navbar" onRecipeClick={handleLoadSavedMeals} onRankingClick={handleRankingClick} />
    </Container>
  );
};
export default App;